import React from "react"
import { Link as GatsbyLink } from "gatsby"

import "./Link.css"

export default function Link({ children, href, ...rest }) {
  return href ? (
    <a className="rLink" href={href} {...rest}>
      {children}
    </a>
  ) : (
    <GatsbyLink className="rLink" {...rest}>
      {children}
    </GatsbyLink>
  )
}
