import { Link as GatsbyLink } from "gatsby"
import PropTypes from "prop-types"
import React from "react"

const Navbar = ({ siteTitle }) => (
  <header
    style={{
      background: "#72a4a4",
      marginBottom: `1.45rem`,
    }}
  >
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}
    >
      <h1 style={{ margin: 0 }}>
        <GatsbyLink
          to="/"
          style={{
            color: "#f7f7f8",
            textDecoration: `none`,
            textShadow: "rgba(0, 0, 0, .2) -5px 5px 8px",
          }}
        >
          {siteTitle}
        </GatsbyLink>
      </h1>
    </div>
  </header>
)

Navbar.propTypes = {
  siteTitle: PropTypes.string,
}

Navbar.defaultProps = {
  siteTitle: ``,
}

export default Navbar
